body {
  margin: 0;
  line-height: normal;
}

:root {
  /* fonts */
  --font-times-new-roman: "Times New Roman";
  --font-roboto: Roboto;
  --font-arial: Arial;
  --font-outfit: Outfit;

  /* font sizes */
  --font-size-7xs: 6px;
  --font-size-6xs: 7px;
  --font-size-4xs: 9px;
  --font-size-base: 16px;
  --font-size-2xs: 11px;

  /* Colors */
  --color-black: #000;
  --color-white: #fff;
  --color-darkslateblue-100: #153e90;
  --color-slategray: #5c6482;
  --color-gray: #1e1e1e;
  --color-lavender: #dbe3f3;

  /* border radiuses */
  --br-8xs: 5px;
  --br-3xs: 10px;
}
